import { store } from "../redux/store";

export const getToken = () => {
  return new Promise((resolve, reject) => {
    let token = null;
    // const oldState = store.getState();
    // const state = { ...oldState };
    // Try to get token from state
     const { socialAffilAdmin } = store.getState();

    if (socialAffilAdmin && socialAffilAdmin.socialAffilAdminToken) {
      token = socialAffilAdmin.socialAffilAdminToken;
    }
    resolve(token);
  });
};
