export const uploadFileMultiPart = async (
  attachToken = false,
  formData,
  mediaType = 'image'
) => {
  let headers = {};

  if (attachToken) {
    try {
      const authToken = await getToken();
      if (authToken) {
        headers["Authorization"] = "Bearer " + authToken;
      }
    } catch (e) {
      console.log("Error fetching auth token: ", e);
    }
  }

  return new Promise((resolve, reject) => {
    try {
      fetch(cloudinaryURL, {
        method: "POST",
        headers: headers,
        body: formData
      })
        .then(
          res => res.json(),
          error => {
            reject(error);
          }
        )
        .then(
          jsonResponse => {
            resolve(jsonResponse);
          },
          error => {
            reject(error);
          }
        )
        .catch(error => {
          reject(error);
        });
    } catch (e) {
      console.log(e);
      reject();
    }
  });
};
