import React, { Component } from "react";
import { Link } from "react-router-dom";
import {
  Container,
  Row,
  Col,
  // Table,
  Button,
  Input,
   // FormGroup,
  // Label,
  // Modal,
  // ModalBody,
  // ModalFooter,
  // ModalHeader
} from "reactstrap";
import CustomTable from "../components/custom-table";
import {
  getAllSeller,
  editSeller
} from "../http/http-calls";
import config from "../config";
import { ToastsStore } from "react-toasts";
import AddEditSellerModal from "../components/add-seller-modal";
import { parsePhoneNumberFromString } from "libphonenumber-js";
// import CopyclipBoard from "../../components/copy-clipboard";
// import CopyclipBoard from "../components/copy-clipboard";
import { format } from "date-fns";
// import TextEditor from "../components/text-editor";
import { 
  showToast, 
  // deepClone 
} from "../helper-methods";
// import CustomDataTable from "../components/custom-data-table";
// import { parsePhoneNumberFromString } from "libphonenumber-js";

class Seller extends Component {
  state = {
    addSellerModal: {
      isOpen: false,
      data: null,
      type: "add",
    },
    sellerList: [],
    sellerListBackup: [],
    headerKeys: [
      { id: "id", label: "id" },
      { id: "sellerStore", label: "Seller Store" },
      { id: "name", label: "Name" },
      { id: "email", label: "Email" },
      { id: "phone", label: "Phone" },
      { id: "products", label: "Products" },
      { id: "sales", label: "Sales" },
      { id: "status", label: "Status" },
      // { id: "creator", label: "Creator" },
      // { id: "price", label: "Price" },
      // { id: "items", label: "Items" },
      // { id: "flag", label: "Flag" },
      // { id: "joinedAt", label: "Joined" },
      // { id: "subscriptions", label: "Subscriptions" },
      // { id: "tipAmount", label: "Tips" },
      // { id: "spent", label: "Lifetime Spend" },
      // { id: "status", label: "Status" },
      // { id: "action", label: "Action", noSort: true }
    ],
    salesCounts: config.salesCounts,
    filters: {
      status: "",
      search: "",
      sales: "",
    },
    loading: {
      loadingData: false,
      changeStatusLoading: false,
    },
  };

  componentDidMount() {

    this._getAllSeller()

  }

  _getAllSeller = () => {

    this._manageLoading("data", true);

    getAllSeller()
    .then(res => {
      console.log('api res: ',res)
      
      const sellerList = res.sellers.map(e => {
        return {
          id: e._id,
          sellerStore: e.storeName,
          name: `${e.name.first} ${e.name.last}`,
          phone:  e.phone && e.phone !== '-' ? e.phone : '-',
          email: e.email,
          status: e.isActive ? 'Active' : 'Inactive',
          products: e.productCount,
          sales: e.totalEarned
        }
      })

      this.setState({sellerList: sellerList, sellerListBackup: sellerList})
      this._manageLoading("data", false);

    })
    .catch(err => {
      this._manageLoading("data", false);

      console.log('api error: ', err)
      showToast(
        err.reason && err.reason.length
          ? err.reason
          : "Server error. Try again after sometime.",
        "error"
      );
    })
  }
  
  _formatDate = (date) => {
    // console.log("TCL: Subscribers -> _formatDate -> date", date);
    return format(new Date(date), "MMM d, yyyy");
  };

  _onToggleSellerModal = (type, data) => {
    // console.log(type, data);
    let { addSellerModal } = JSON.parse(JSON.stringify(this.state));
    addSellerModal.isOpen = !addSellerModal.isOpen;
    addSellerModal.type = type;
    addSellerModal.data = data;
    this.setState(
      {
        addSellerModal,
      },
      () => {
        console.log(this.state);
      }
    );
  };

  _manageLoading = (key, value) => {
    let {
      loadingData,
      changeStatusLoading,
    } = this.state.loading;
    if (key === "data") {
      loadingData = value;
    } else if (key === "change-status") {
      changeStatusLoading = value;
    }
    this.setState({
      loading: { loadingData, changeStatusLoading },
    });
  };

  _onStatusUpdate = (data) => {
    
    this._manageLoading("change-status", true);

    const { sellerList } = this.state;
    let singleSeller = sellerList.find((each) => each.id === data.id);

    if(!singleSeller) {
      console.log(data.id, sellerList)
      this._manageLoading("change-status", false);
      ToastsStore.error("Somthing went wrong!", 3000);
      return;
    }

    let status = {}

    if(singleSeller.status.toLowerCase()==='active') {
        status['isActive'] = false;
        singleSeller.status = 'Inactive'
    } else {
        status['isActive'] = true;
        singleSeller.status = 'Active'
    }
    
    editSeller(status, data.id)
    .then((response) => {
        console.log('api res: ',response);

        this.setState({
          sellerList,
          sellerListBackup: sellerList,
      });

        this._manageLoading("change-status", false);
        ToastsStore.success("Status changed Successfully!", 3000);
      })
      .catch((error) => {
        console.log('api error: ',error);
        // ToastsStore.error(error.reason, 3000);
        this._manageLoading("change-status", false);
        showToast(
          error.reason && error.reason.length
            ? error.reason
            : "Server error. Try again after sometime.",
          "error"
        );
      });
  };

  _dataFormat = (cell, row, header) => {
    if (header === "sellerStore") {
      return (
        <div
          color="ghost-primary"
          onClick={() => this._onToggleSellerModal("edit", row)}
          style={{ cursor: "pointer" }}
        >
          {row.sellerStore}
        </div>
      );
    } else if (header === "phone") {
      return row.phone !== "-" ? (
        parsePhoneNumberFromString(row.phone) ? parsePhoneNumberFromString(row.phone).formatInternational() : row.phone
      ) : (
        "-"
      );
    } else if (header === "sales") {
      return row.sales > 0 ? (
        //<span onClick={() => this._onToggleVaultModal(row.salesList)}>
          row.sales
        //</span>
      ) : (
        0
      );
    } else if (header === "products") {
      return row.products > 0 ? (
          row.products
      ) : (
        0
      );
    } else if (header === "status") {
      return (
        <Input
          type="select"
          style={{width: 85, minWidth: 85}}
          value={cell}
          onChange={() => this._onStatusUpdate(row)}
          disabled={this.state.loading.changeStatusLoading}
        >
          <option value="Active">Active</option>
          <option value="Inactive">Inactive</option>
        </Input>
      );
    } else if (header === "action") {
      return (
        <Link to={`/subscriber-view/${row.id}`}>
          <Button color="ghost-primary">View</Button>
        </Link>
      );
    } else {
      return cell;
    }
  };

  _filterOnChange = ({ currentTarget }) => {
    let { filters } = this.state;

    filters[currentTarget.name] = currentTarget.value;
    this.setState({ filters });
    // if (currentTarget.name !== "search") {
      this._filterSellerList();
    // }
  };

  _filterSellerList = () => {
    let filterConditions = [];
    // console.log(this.state);
    let { filters, sellerListBackup, sellerList } = this.state;
    Object.keys(filters).forEach((key) => {
      if (filters[key]) {
        filterConditions.push(key);
      }
    });
    sellerList = sellerListBackup;

    if (filterConditions.length) {
        if (filters.sales) {
          const salesFilter = JSON.parse(filters.sales)
          if(salesFilter.max === 0 && salesFilter.min === 0) {
            sellerList = sellerList.filter((each) => {
              return each.sales === 0
            })
          } else if(!salesFilter.max) {
              sellerList = sellerList.filter((each) => {
                return each.sales > salesFilter.min
              })
          } else {
              sellerList = sellerList.filter((each) => {
                return each.sales > salesFilter.min && each.sales <= salesFilter.max
              })
          }
          // console.log(salesFilter, sellerList)
        }
        if (filters.status) {
          sellerList = sellerList.filter((each) => {
            return each.status.toLowerCase() === filters.status.toLowerCase();
          });
        }
        if (filters.search.trim().length) {
          const searchFilter = filters.search.trim().toLowerCase()
          sellerList = sellerList.filter((each) => {
            return (
              each.sellerStore.toLowerCase().includes(searchFilter) ||
              each.name.toLowerCase().includes(searchFilter) ||
              each.email.toLowerCase().includes(searchFilter) ||
              each.phone.toLowerCase().includes(searchFilter)
            );
          });
        }
        this.setState({ sellerList });
    } else {
        this.setState({ sellerList: sellerListBackup });
    }
  }

  render() {
      const {
        filters,
        loading,
        sellerList,
        headerKeys
      } = this.state;
      //  console.log("this.state",this.state)

      return (
      <div className="app TruFansPgBg animated fadeIn">
        <Container fluid>
          <Row>
            <Col xs="12">
              <div className="PgTitle">
                <h2>Sellers</h2>

                <Button
                  className="BtnPurple"
                  onClick={() => this._onToggleSellerModal("add", null)}
                >
                  New Seller
                </Button>
              </div>
              {/* filters */}
              {!loading.loadingData && (
                <div>
                  <div className="filterWrap">
                    <div className="d-flex align-items-center">
                      <i className="fa fa-filter"></i>

                      <Input
                        type="select"
                        name="sales"
                        value={filters.sales}
                        onChange={this._filterOnChange}
                      >
                        <option value="">Sales</option>
                        {/* {config.salesCounts.map((option, index) => (
                          <option key={index} value={JSON.stringify(option.value)}>                        
                            {index === config.salesCounts.length-1 || (option.value.min===0 && option.value.max===0)? '': '<'} ${option.label}
                          </option>
                        ))} */}
                      </Input>
                      <Input
                        type="select"
                        name="status"
                        value={filters.status}
                        onChange={this._filterOnChange}
                      >
                        <option value="">Status</option>
                        <option value="active">Active</option>
                        <option value="inactive">Inactive</option>
                      </Input>
                    </div>

                    <div className="d-flex align-items-center">
                      <Input
                        type="text"
                        id="search"
                        name="search"
                        placeholder="Search"
                        autoComplete="off"
                        className="ml-2"
                        value={this.state.filters.search}
                        onChange={this._filterOnChange}
                      />

                      <Button
                        className="filterBtn"
                        onClick={() => this._filterSellerList()}
                      >
                        <i className="icon-magnifier"></i>
                      </Button>
                    </div>
                  </div>{" "}

                  {/* filterWrap */}
                  <CustomTable
                    tableData={sellerList}
                    headerKeys={headerKeys}
                    dataFormat={this._dataFormat}
                  ></CustomTable>
                  
                </div>
              )}
              {loading.loadingData && (
                <div className="filterWrap">
                  <div className="loading-section list-loading">
                    <i className="fa fa-spinner fa-spin"></i> &nbsp; Loading
                    Sellers..
                  </div>
                </div>
              )}
              {/* The below div will appear after the user selects row(s) from the above table */}

              {/* Modal for "Subscriptions" */}
              <AddEditSellerModal
                type={this.state.addSellerModal.type}
                data={this.state.addSellerModal.data}
                isOpen={this.state.addSellerModal.isOpen}
                toggle={() => this._onToggleSellerModal()}
                reloadSellerList={this._getAllSeller}
              ></AddEditSellerModal>
              
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default Seller;
