import {
  makeGetRequest,
  makePostRequest,
  makePutRequest,
  // makeDeleteRequests,
  uploadFileMultiPart,
} from "./http-service";

const baseURL= "http://139.59.14.81:3000/admin/v1/"
const publicURL= "http://139.59.14.81:3000/api/v1/"
const cloudinaryURL= "https://api.cloudinary.com/v1_1/www-logic-square-com/auto/upload?upload_preset=yu5mntw0"

export const login = ({ handle, password }) => {
  return new Promise((resolve, reject) => {
    makePostRequest(baseURL + "login", false, {
      handle,
      password,
    })
      .then((res) => {
        resolve(res);
      })
      .catch((e) => {
        console.log("API call error: ", e);
        reject(e);
      });
  });
};

